.menu {
    display: flex;
    flex-direction: row;
    position: fixed;
    top: 0;
    height: 80px;
    background: $dark!important;
    width: 100%;
    z-index: 6;
    align-items: left;
    ul {
        height: 80px;
        text-align: center;
    }
}
h1.head{
    padding-left: 15px;
}

.header-wrap {
    max-width: 1440px;
}
.header-logo {
    height: 53px;
    z-index: 999;
    padding: .5em 0;
}
.navbar-brand{
    padding: .55em 0;
    z-index: 1000;
}


.nav-list {
    display: flex;
    flex-direction: row;

 /*    width: 80%; */
    padding-left: 0;
}

.menu ul {
    height: 80px;
    text-align: center;
 /*    margin-right: 150px; */
}

a.nav-link {
font-size: 14px;
font-weight: 400;
color: $white!important;
margin-top: 0;
padding: 29px 20px !important;

}
.nav-list li {
    height: 80px;
    font-size: 1em;
    color: #153559;
    margin-top: 0 !important;
    
}
.nav-list li:hover {
    background: $dark-blue;
    &:nth-of-type(1) {
        background: $blue!important;
    }
    &:nth-of-type(2) {
        background: $green!important;
    }
    &:nth-of-type(3) {
        background: $yellow !important;
    }
}
#kontakt:hover {
  background: $dark-blue;
}


.language-list {
    margin-left: 1em;

}
a.language {
    display: inline-block;
    color: $blue !important;
    font-size: 14px;

    &.nav-link:hover {
        color: $white !important;
        background: $dark-blue;
    }
}
.naving {
    display: block;
    margin-left: 3em;
}
.nav-right {
    display: inline-block;
    float: left;
}

.navigation {
    display: none;
}

.dropdown:hover .dropdown-submenu {
    width: 100%;
    visibility: visible;
    opacity: 1;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
  
.dropdown-submenu {
    top: -2px;
    width: 500px;
    font-size: 14px;
    box-shadow: 0px 3px 9px 0px rgba(0, 0, 0, 0.12);
    border: 0;
    top: 82px;
    left: -25px;
    border-radius: 0;
    display: block;
    visibility: hidden;
    transition: .3s ease;
    opacity: 0;
    -webkit-transform: translateY(20px);
    transform: translateY(20px);
    background: $dark;
  }



  .overlay{
    z-index: 5
}

.dropdown-submenu {
    width: 100%;
    background: $dark;
    color: $white;
    top: -2px;
    a{
        color: $white;
        &:hover {
            background: $dark-blue;
            color: $white;
        }
    }
}
.box{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 40%;
    left: 0;
    margin: 0 auto;
    p {
        font-size: 20px;
      }
    a {
        display: inline-block;
        position: relative;
        text-align: center;
        text-decoration: none;
        font-size: 20px;
        overflow: hidden;
        top: 5px;
      }
  }
 
  
  .box a:after {
    content: '';
    position: absolute;
    height: 2px;
    width: 0%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    left: 50%;
    bottom: 0;
    transition: .35s ease;
  }
  .box a:hover:after, .box a:focus:after, .box a:active:after {
    width: 100%;
  }

  
  .button_container {
      background-color: $dark;
    position: fixed;
    top: 0;
    right: 0;
    height: 80px;
    width: 80px;
    cursor: pointer;
    z-index: 100;
    transition: opacity .25s ease;
    color: $white;
  }
  .button_container:hover {
    background: $blue;
  }
  .button_container.active {
    .top {
        -webkit-transform: translateY(11px) translateX(0) rotate(45deg);
                transform: translateY(11px) translateX(0) rotate(45deg);
        background: $white;
      }
      .middle {
        opacity: 0;
        background: #FFF;
      }
      .bottom {
        -webkit-transform: translateY(-11px) translateX(0) rotate(-45deg);
                transform: translateY(-11px) translateX(0) rotate(-45deg);
                background: $white;
      }

  } 


  .button_container span {
    background: #fff;
    border: none;
    height: 2px;
    width: 40%;
    position: absolute;
    top: 30px;
    left: 25px;
    transition: all .35s ease;
    cursor: pointer;
    &:nth-of-type(2) {
        top: 40px;
        width: 35%;
      }
    &:nth-of-type(3) {
        top: 50px;
      }  
  }



  .overlay {
    position: fixed;
    background: $dark;
    top: 80px;
    left: 0;
    width: 100%;
    height: 0%;
    opacity: 0;
    visibility: hidden;
    transition: opacity .35s, visibility .35s, height .35s;
    overflow: hidden;
  }
  .overlay.open {
    opacity: .95;
    visibility: visible;
    height: 100%;
  }


  
  .overlay.open li {
    -webkit-animation: fadeInRight ease forwards;
            animation: fadeInRight ease forwards;
    -webkit-animation-delay: .35s;
            animation-delay: .35s;
            &:hover {
                background: $dark-blue;
                &:nth-of-type(1) {
                    background: $blue;
                }
            }
  }


  .overlay.open li:nth-of-type(2) {
    -webkit-animation-delay: .4s;
            animation-delay: .4s;
            &:hover {
                background: $green;
            }
  }
  .overlay.open li:nth-of-type(3) {
    -webkit-animation-delay: .45s;
            animation-delay: .45s;
            &:hover {
                background: $yellow;
            }
  }
  .overlay.open li:nth-of-type(4) {
    -webkit-animation-delay: .50s;
            animation-delay: .50s;
  }
  .overlay.open li:nth-of-type(5) {
    -webkit-animation-delay: .55s;
            animation-delay: .55s;
  }
  .overlay.open li:nth-of-type(6) {
    -webkit-animation-delay: .6s;
            animation-delay: .6s;
  }






  .overlay nav {
    position: relative;
    height: 100%;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-size: 1.2em;
    font-weight: 400;
    text-align: center;
  }
  .overlay ul {
    list-style: none;
    padding: 0;
    margin: 0 auto;
    display: inline-block;
    position: relative;
    height: 65%;
    width: 100%;
  }
  .overlay ul li {
    display: block;
    margin-top: .5em;
    height: calc(100% / 15);
    min-height: auto;
    position: relative;
    opacity: 0;
  }
  .overlay ul li a {
    display: block;
    position: relative;
    color: #FFF;
    text-decoration: none;
    padding: .7em;
  }
