@media (max-width: 1390px) {
    a.nav-link  {
        font-size: 14px;
        padding: 29px 10px !important;
    }
    a.nav-link.language {
        font-size: 14px;
    }
    .navbar-brand {
        padding: .7em 0;
    }
    .header-logo {
        height: 45px;
    }
}


@media (max-width: 1200px) {
    .nav-list {
        display: none;
    }
    .navigation {
        display: inline;
    }
    a.nav-link.language {
        font-size: 1.2em;
    }
    .navbar-brand {
        padding: .7em 0;
    }
    .header-logo {
        height: 50px;
    }
}
@media screen and (min-width:900px) {
.overlay nav {
     
    font-size: 1.2em;
    font-weight: 400;
